/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps, InspectorControls, RichText, InnerBlocks } from '@wordpress/block-editor';

import {
	TextControl,
	PanelBody,
	PanelRow,
	ColorPicker
} from '@wordpress/components';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */
export default function Edit(props) {

	const {
		attributes:{Handle, Platform, Accent, Link},
		setAttributes,
		className,
	} = props

	const onChangeHandle = (newContent) =>{
		setAttributes( { Handle: newContent } );
	}
	const onChangePlatform = (newContent) =>{
		setAttributes( { Platform: newContent } );
	}
	const onChangeAccent= (newContent) =>{
		setAttributes( { Accent: newContent } );
	}

	const onChangeLink= (newContent) =>{
		setAttributes( { Link: newContent } );
	}

	let AccentColor = {
		color:Accent
	}
	const AccentColoring = {style: AccentColor}

	let BorderAccentColor = {
		borderLeftColor:Accent,
		borderStyle:'solid',
		borderLeftWidth:'0.5em'
	}




	return (
		<a { ...useBlockProps({style:BorderAccentColor}) }>

			<InspectorControls>
				<PanelBody>
					<PanelRow>
						<TextControl
							label={ __( 'Social Handle', 'basic-block' ) }
							value={ Handle }
							help={ __(
								'Social media handle including @ if applicable'
							) }
							onChange={onChangeHandle}
						/>
					</PanelRow>	
					<PanelRow>
						<TextControl
							label={ __( 'Platform', 'basic-block' ) }
							value={ Platform }
							help={ __(
								'Social media platform for handle'
							) }
							onChange={onChangePlatform}
						/>
					</PanelRow>
					<PanelRow>
						<TextControl
							label={ __( 'Link', 'basic-block' ) }
							value={ Link }
							help={ __(
								'Link to social channel'
							) }
							onChange={onChangeLink}
						/>
					</PanelRow>
					<PanelRow>
						<ColorPicker
							label={ __( 'Accent Block', 'basic-block' ) }
							value={Accent}
							help={ __(
								'Accent color'
							) }
							onChange={onChangeAccent}
						/>
					</PanelRow>
				</PanelBody>
			</InspectorControls>

			<p class="handle">{Handle}</p>
			<p class="platform"><span {...AccentColoring}>on</span>{Platform}</p>
		</a>
	);
}
